import React from 'react'
import '../styles/CooperationModal.css'

const CooperationModal = (props) => {
  return (
    <aside style={props.showModal ? { top: '50%', left: '50%', } : { top: '50%', left: '-100%' }}>
      <div onClick={() => props.hiddenModal()} className='xBtn'><i className="fas fa-times"></i></div>
      <div className='modal1' onClick={() => props.toggleModal1()}>How it work - Treningi Personalne</div>

      <span>DARMOWY TRENING PRBNY!</span>
      <ul className='personal'>
        <li>Przed rozpoczciem pierwszego treningu, przeprowadzana jest szczegowa ankieta majca na celu okreli poziom wytrenowania klienta, stan zdrowia oraz oczekiwania i preferencje.</li>
        <li>Przed rozpoczciem pierwszej sesji treningowej, wykonujemy trening prbny dziki ktremu dowiesz si jak pracuje podczas treningu, oraz ustalimy szczegy i oczekiwania co do wsppracy.</li>
        <li>Kady pakiet treningowy jest do zrealizowania w cigu 30 dni.</li>
        <li>Trening personalny mona przeoy lub odwoa maksymalnie 24 godziny przed umwionym terminem.</li>
        <li>Treningi personalne s przeprowadzane na siowni Gym Corner ( Stare Club S4 Fitness ) przy ulicy Nowogrodzkiej 50/54.</li>
        <li>Kady trening trwa od 60 do 90 minut.</li>
        <li>Przy pakietach treningowych masz dodatkowo gotowy plan treningowy i plan dietetyczny oraz kontakt mailowy/telefoniczny ze mn.</li>
        <li>Zastrzegam sobie prawo do odmowy kontynuowania wsppracy jeli podopieczny nie stosuje si do wytycznych.</li>
        <li>W przypadku zakoczenia wsppracy, informacj taka naley przekaza na pocztku ostatniego pakietu - czyli miesic przed.</li>
      </ul>

      <div className='modal2' onClick={() => props.toggleModal2()}>How it work - Plany Dietetyczny i Treningowe Online</div>

      <ul className='online'>
        <li>Przed rozpoczciem wsppracy przeprowadzana jest szczegowa ankieta majca okreli poziom wytrenowania podopiecznego, stan zdrowia oraz oczekiwania i preferencje.</li>
        <li>Patno naley uregulowa po wysaniu uzupenionej ankiety. Po zaksigowaniu opaty, drog mailow wysyany jest plan dietetyczny/treningowy.</li>
        <li>Przy zakupie pakietu " Plan Dietetyczny + Plan Treningowy " podopieczny ma peny zakres konsultacji tzn. odpowiadam na pytania dotyczce diety i treningu, udzielam porad, kontroluj Twoj postpy i wyniki, dbam o utrzymanie dobrego nastawienia na cel.</li>
        <li>Zastrzegam sobie prawo do odmowy kontynuowania wsppracy jeli podopieczny nie stosuje si do wytycznych.</li>
        <li>Zastrzegam sobie prawo do materiaw ktre wysyam tj. plany treningowe i dietetyczne, oraz nie pozwalam na ich dalsze rozpowszechnianie.</li>
        <li>Wszystkie informacje/zdjcia uzyskane poprzez ankiet,bd przechowywane na prywantym dysku, i nie bd one udostpniane nikomu innemu poza moj osob.</li>
        <li>Kady klient ktry podejmuj wspprac ze mn, jednoczenie akceptuje wyej wymienione warunki.</li>
      </ul>
    </aside>
  )
}

export default CooperationModal