import React from 'react'
import $ from 'jquery';
import Offer from './Offer'
import Cooperation from './Cooperation'
import CooperationModal from './CooperationModal'
import img1 from '../IMG/oneBlue.png'
import img2 from '../IMG/twoBlue.png'
import img3 from '../IMG/threeBlue.png'
import img4 from '../IMG/fourBlue.png'
import img5 from '../IMG/oneRed.png'
import img6 from '../IMG/twoRed.png'
import img7 from '../IMG/threeRed.png'
import img8 from '../IMG/fourRed.png'
import '../styles/SectionTwo.css'

class SectionTwo extends React.Component {
  state = {
    cooperationBtn: false,
    showModal: false,
    offersTxt: [
      { title: 'PLAN DIETETYCZNY', active: false, id: 1, imgNumber: img1, imgNumber2: img5, txt: 'Plan ywieniowy, dopasowany do Twoich potrzeb i aktualnej aktywnoci fizycznej. Niezalenie czy chcesz spali tkank tuszczow, czy zbudowa mas miniow. Po zamwieniu planu dietetycznego otrzymasz na maila plik ze szczegow ankiet, ktra pozwoli mi stworzy najskuteczniejsz opcj dla Ciebie, tak aby mg uzyska zaoone cele. Dieta bdzie zawieraa wszystkie potrzebne informacje, a Ty bdziesz mg samodzielnie komponowa smaczne a zarazem dietetyczne posiki, trzymajc si moich wytycznych.' },
      { title: 'PLAN TRENINGOWY', active: false, id: 2, imgNumber: img2, imgNumber2: img6, txt: 'Plan treningowy, tak samo jak dieta, jest niezbdny do uzyskania wymarzonej sylwetki. Jednake jeli speniasz ju pierwszy warunek, w postaci zbilansowanej, odpowiednio dobranej diety, mog Ci zaproponowa plan treningowy, dopasowany do Twoich potrzeb i diety. Bo wybraniu tej opcji, bdziesz musia uzupeni ankiet, bym mg przygotowa idealny dla Ciebie i Twoich celw trening, oparty na wiczeniach odpowiednio dobranych do Twojego aktualnego wytrenowania i moliwoci.' },
      { title: 'PLAN TRENINGOWY I DIETETCZNY', active: false, id: 3, imgNumber: img3, imgNumber2: img7, txt: 'W tym pakiecie, proponuje Ci wspomniany wyej Plan Dietetyczny oraz Plan Treningowy. Jest to opcja bardziej ekonomiczna i na pewno bardziej skuteczna, poniewa bd mia peny wgld i kontrol Twoich postpw i wynikw. Dodatkowo masz moje pene wsparcie tzn. masz w tym peen zakres konsultacji przez ten okres - odpowiadam na pytania , wprowadzam zmiany do diety oraz treningu, oceniam Twoje postpy.' },
      { title: 'TRENINGI PERSONALNE', active: false, id: 4, imgNumber: img4, imgNumber2: img8, txt: 'Treningi Personalne ze mn gwarantuj Ci przede wszystkim pewno, e wykonywane przez Ciebie wiczenia bd w caoci zachowywa poprawn technik, a trening bdzie na najwyszym poziomie. Dostosuj godziny spotka, pod Twj czas pracy, zaj poza prac, harmonogramu tygodnia czy miesica. Wsplnymi siami, na pewno osigniemy wyznaczony przez Ciebie cel, poniewa nie pozwol Ci si podda, przed jego osigniciem. Jeli zaley Ci na optymalnych efektach, i chcesz by kady element Twojego trenigu by maksymalnie doszlifowany, to ta opcja jest dla Ciebie najlepsza.' }
    ]
  }

  modal1Flag = true
  modal2Flag = true

  showModal = () => {
    this.setState({
      showModal: true
    })
  }

  hiddenModal = () => {
    this.setState({
      showModal: false
    })
  }

  toggleClassModal1 = () => {
    if (this.modal1Flag) {
      document.querySelectorAll('ul.personal li').forEach((l) => {
        l.classList.toggle('active')
      })
      setTimeout(() => {
        $('ul.personal').toggleClass('active')
      }, 50)
      this.modal1Flag = false
    } else {
      $('ul.personal').toggleClass('active')

      setTimeout(() => {
        document.querySelectorAll('ul.personal li').forEach((l) => {
          l.classList.toggle('active')
        })
      }, 50)
      this.modal1Flag = true
    }
  }

  toggleClassModal2 = () => {
    if (this.modal2Flag) {
      document.querySelectorAll('ul.online li').forEach((l) => {
        l.classList.toggle('active')
      })
      setTimeout(() => {
        $('ul.online').toggleClass('active')
      }, 50)
      this.modal2Flag = false
    } else {
      $('ul.online').toggleClass('active')

      setTimeout(() => {
        document.querySelectorAll('ul.online li').forEach((l) => {
          l.classList.toggle('active')
        })
      }, 50)
      this.modal2Flag = true
    }
  }

  handleClickOffer = (id) => {
    const offersTxt = [...this.state.offersTxt]


    offersTxt.forEach((offer) => {
      if (id === offer.id) {
        offer.active = !offer.active
      }
    })

    return this.setState({
      offersTxt
    })
  }

  render() {
    const offersTxtBlue = this.state.offersTxt.map((offer) => (
      <Offer
        id={offer.id} key={offer.id}
        title={offer.title}
        active={offer.active}
        imgNumber={offer.imgNumber}
        imgNumber2={offer.imgNumber2}
        txt={offer.txt}
        click={this.handleClickOffer}
        color={this.props.color}
      />
    ))

    return (
      <>
        <section className="sectionTwo">
          <h2 style={this.props.color ? { color: 'rgba(0,100,100,1)' } : { color: 'rgba(150,0,0,1)' }}>WYBIERZ INTERESUJCY CI PAKIET</h2>
          <h3 style={this.props.color ? { color: 'rgba(0,100,100,1)' } : { color: 'rgba(150,0,0,1)' }}>PAKIETY ZOSTAY PODZIELONE NA CZTERY KATEGORIE, TAK BY MG WYBRA INTERESUJCY
          CI ELEMENT TWOJEGO DENIA DO LEPSZEJ SYLWETKI, ORAZ WSPANIAEGO SAMOPOCZUCIA.</h3>
          <div style={this.props.opacity ? { opacity: '1' } : { opacity: '0' }} className='articleWrap'>
            {offersTxtBlue}
          </div>
          <Cooperation
            color={this.props.color}
            click={this.showModal}
          />
        </section>
        <CooperationModal
          showModal={this.state.showModal}
          hiddenModal={this.hiddenModal}
          toggleModal1={this.toggleClassModal1}
          toggleModal2={this.toggleClassModal2}
          color={this.props.color}
        />
      </>
    )
  }
}

export default SectionTwo


