import React from 'react'
import Banner from './Banner'
import Me from '../IMG/me.jpg'
import '../styles/SectionOne.css'

const txtBanner = ['Dietetyka Jest dla mnie osobn pasj, poniewa wierz e to wanie sposb w jaki si odywiamy, jest kluczem do zdrowia i do wymarzonej sylwetki. Oprcz tego czy si ona z jeszcze inn pasj, jak jest gotowanie. Uwaam e to wanie to podejcie, pozwala mi tak bardzo angaowa si w to co robi i pomaga ludziom w deniu do ich wymarzonej sylwetki.',
    'Plany dietetyczne oraz treningowe S tworzone tylko przeze mnie, w oparciu o szczegowe informacj ktre otrzymam od Ciebie w ankiecie, jak dostaniesz ode mnie przed rozpoczciem wsppracy. Dziki temu, kady plan jaki ode mnie otrzymasz, jest idealnie dopasowany do Ciebie, do Twojego poziomu wytrenowania, Twoich preferencji i oczekiwa, a co najwaniejsze, kady plan jest uoony tak, by osign wyznaczony przez siebie cel, w jak najszybszy i najskuteczniejszy sposb. S rozpisywane nie tylko w oparciu o wiedz jak zdobyem na przestrzeni wszystkich lat pracy nad wasn sylwetk. Jest w nich rwnie wykorzystywana wiedza, dowiadczenie i metody, najlepszych Polskich autorytetw w tej dziedzinie, na ktrych przez cay czas wzorowaem si, chonc ca wiedz jak przekazywali, i sprawdzajc najskuteczniejsze metody ksztatowania sylwetki,jednoczenie wypracowujc swoje unikalne podejcie.',
    'Badania Krwi Nie jestem fanem tego by Trener Personalny / Dietetyk bawi si w lekarza, uwaam e wiele wicej mona osign przy wsppracy obydwu stron, jednoczenie wiem e coraz ciej znale odpowiedniego lekarza, ktry na czas zaalarmuje nas o zaistniaym lub nadchodzcym problemie, oraz wyjani jak mona nam pomc, nie bagatelizujc naszego problemu czekajc do ostatniej chwili. Dlatego na podstawie Twoich bada krwi oraz szczegowej rozmowie, mog podpowiedzie z czym mamy do czynienia i jak mona temu zaradzi, jakie dodatkowe badania naley wykona oraz gdzie skierowa dalsze kroki, dopasowujc do tego odpowiedni diet i aktywno fizyczn.']

const colorDarkCyan = 'rgba(0,100,100,0.75'
const colorFireBrick = 'rgba(150,0,0,0.75)'

class SectionOne extends React.Component {
    render() {
        return (
            <>
                <section className="sectionOne" >
                    <article className="about">
                        <img src={Me} alt="Mateusz Ratyski" />
                        <div className="txt">
                            <p> <span style={this.props.color ? { color: colorDarkCyan } : { color: colorFireBrick }}>O Mnie</span> <br /> Urodziem si 05 maja 1992 roku. Od 2011 roku jestem pasjonatem wszelkiego rodzaju aktywnoci fizycznej i zdrowego trybu ycia.
                             Zdecydowanie najbardziej interesuje mnie kwestia budowania i ksztatowania sylwetki majc jednoczenie na uwadze wszelkie zdrowotne aspekty tego procesu.
                      Nie zwaajc na to czy jest to budowanie masy miniowej czy redukcja tkanki tuszczowej.</p>
                            <p> <span style={this.props.color ? { color: 'rgba(0,100,100,0.75' } : { color: 'rgba(150,0,0,0.75)' }}>Szkolenia i kursy</span> <br />  w ktrych braem udzia i warto je wymieni to na pewno kurs Instruktora Fitness i Kulturystki w World Sport Academy. Uzyskaem tam rwnie certyfikat Profesjonalnego Instruktora Sportu, Fitness i Kulturystki, Profesjonalnego doradcy suplementacji oraz Dyplom Instruktora sportw siowych.
                     Oprcz tego na innych szkoleniach uzyskaem certyfikat Wielopoziomowej Dietetyki i Suplementacji, dalej certyfikat Podstaw Dietoterapii oraz certyfikat ywienia przy Cukrzycy typu 2, Hipoglikemii, Zespou metabolicznego, Niedoczynnoci/Nadczynnoci tarczycy, Hashimoto i Insulinoopornoci.</p>
                        </div>
                    </article>
                    <article className='mySkills'>
                        <div className='skillDiv' style={this.props.opacity1 ? { opacity: '1' } : { opacity: '0' }}> <Banner className='art1' tittle='Trening' txt={txtBanner[0]} /></div>
                        <div className='skillDiv' style={this.props.opacity2 ? { opacity: '1' } : { opacity: '0' }}><Banner className='art2' tittle='Dieta' txt={txtBanner[1]} /></div>
                        <div className='skillDiv' style={this.props.opacity3 ? { opacity: '1' } : { opacity: '0' }}><Banner className='art3' tittle='Badania Krwi' txt={txtBanner[2]} /></div>
                    </article>
                </section>
            </>
        )
    }
}

export default SectionOne